export default {
  props: ['query'],
  data () {
    return {
      keyword: this.query
    }
  },
  methods: {
    // keyword不为空时，对父组件$emit confirm
    confirmHandler () {
      if (this.keyword) {
        this.$emit('confirm', this.keyword)
      }
    }
  },
  watch: {
    query (newValue) {
      // console.log('kw  ' + newValue)
      this.keyword = newValue
    }
  }
}
